import {fetchOptionsPending, fetchOptionsSuccess} from "../store/order/actions";

export function fetchOrderOptions() {
    return dispatch => {
        dispatch(fetchOptionsPending());
        setTimeout(function () {
            let result = getOptions();
            dispatch(fetchOptionsSuccess(result));
            return result;
        }, 500);
    }
}

